/*SOCIAL MEDIA ICONS*/
#socialIconsContainer{
    -webkit-perspective: 500px;
    -webkit-perspective-origin: 50% 150px;
    z-index: 100;
    position:fixed;
    top:10px;
    right: 15px;
    height: 350px;
    width: 80px;
}
.face {
    position: fixed;
    height: 40px;
    width: 40px;
    padding: 1px;
    background: url("./images/all_logos_crates_web.png");
    background-size:400%;
}
#linkedIn, #twitter, #github, #youTube, #instagram {
    position: fixed;
    right:10px;
    margin: 0 auto;
    height: 40px;
    width: 40px;
    -webkit-transition: -webkit-transform 2s linear;
    -webkit-transform-style: preserve-3d;
    cursor:pointer;
    cursor:hand;
    z-index: 200;
}
@-webkit-keyframes spin {
    from { -webkit-transform: rotateY(0); transform: rotateY(0); }
    to { -webkit-transform: rotateY(180deg); transform: rotateY(180deg); }
}
@keyframes spin {
    from { -webkit-transform: rotateY(0); transform: rotateY(0); }
    to { -webkit-transform: rotateY(180deg); transform: rotateY(180deg); }
}
@-webkit-keyframes unspin {
    from { -webkit-transform: rotateY(180deg); transform: rotateY(180deg); }
    to { -webkit-transform: rotateY(0); transform: rotateY(0); }
}
@keyframes unspin {
    from { -webkit-transform: rotateY(180deg); transform: rotateY(180deg); }
    to { -webkit-transform: rotateY(0); transform: rotateY(0); }
}
#instagram.spin, #linkedIn.spin, #twitter.spin, #github.spin, #youTube.spin {
    -webkit-animation: spin 0.2s linear;
            animation: spin 0.2s linear;
    -webkit-animation-fill-mode: forwards;
}
#instagram.unspin, #linkedIn.unspin, #twitter.unspin, #github.unspin, #youTube.unspin {
    -webkit-animation: unspin 0.2s linear;
            animation: unspin 0.2s linear;
}
#linkedIn {
    top:20px;
}
#github {
    top:80px;
}
#twitter {
    top:140px;
}
#youTube {
